import {ComponentWrapper} from '../essential/aframe-wrapper';
import {EntityBuilder} from '../essential/entity-builder';

interface AASSchema {
   canStart: boolean;
   canCalculate:boolean
}

declare global {
    interface Window { AAS: number; }
}



export class ASSComponent extends ComponentWrapper<AASSchema> {
  constructor() {
    super('aas-component', {
        canStart: {
        type: 'boolean',
        default: false,
      },
      canCalculate: {
        type: 'boolean',
        default: false,
      },
    });
  }

  init() {
  
    let el = this.el;
    let data = this.data ;
    window.timeTaken = 0;
    let timer:any;
    window.AAS = 0;
  
    
    timer = setInterval(() => { 
      if(data.canStart&&data.canCalculate)
      {
        window.AAS ++;   
      //  console.log('AAS'+window.AAS);
      }
        
    }, 1000);
  
  
    
    
    el.addEventListener('stop',function(){
        clearInterval(timer);
    })
 
    
  }

  update() {}

  play() {}

  pause() {}

  tick() {}

  remove() {}

  destroy() {}
}

new ASSComponent().register();



