import {ComponentWrapper} from '../essential/aframe-wrapper';
import {EntityBuilder} from '../essential/entity-builder';

interface TimeResponseSchema {
   canStart: boolean;
}

declare global {
    interface Window { TimeResponse: number; }
}



export class TimeResponseComponent extends ComponentWrapper<TimeResponseSchema> {
  constructor() {
    super('response-component', {
        canStart: {
        type: 'boolean',
        default: false,
      },
    });
  }

  init() {
  
    let el = this.el;
    let data = this.data ;
    window.timeTaken = 0;
    let timer:any;
    window.TimeResponse = 0;
  
    
    timer = setInterval(() => { 
      if(data.canStart)
      {
        window.TimeResponse ++;   
      //  console.log('timeResponse'+window.TimeResponse);
      }
        
    }, 1000);
  
  
    
    
    el.addEventListener('stop',function(){
        clearInterval(timer);
    })
 
    
  }

  update() {}

  play() {}

  pause() {}

  tick() {}

  remove() {}

  destroy() {}
}

new TimeResponseComponent().register();



