import {ComponentWrapper} from '../essential/aframe-wrapper';
import {EntityBuilder} from '../essential/entity-builder';

interface LimitedInteruptionComponentSchema {
  taskInterupt : number;
    canCalculate:boolean;
    canStart:boolean;
}
declare global {
  interface Window { tasksLimitedInterupt: number; }
}

export class LimitedInteruption extends ComponentWrapper<LimitedInteruptionComponentSchema> {
  constructor() {
    super('limited-interuption', {
      taskInterupt: {
        type: 'number',
        default: 0,
      },canCalculate: {
        type: 'boolean',
        default: false,
      },canStart: {
        type: 'boolean',
        default: false,
      },
    });
  }

  init() {
    let el =this.el;
    let data = this.data;
    let interuptionTimer = 0;
    let timer:any;
      
    window.tasksLimitedInterupt = 0; 
  
    
    timer = setInterval(() => { 
      if(data.canCalculate && data.canStart)
      {
        interuptionTimer ++;
        if(interuptionTimer = 2)
        {
          interuptionTimer =0;
         data.taskInterupt ++;    
         data.canCalculate = false;
         console.log('task interrupt');
         if(data.taskInterupt == 3 )
         {
           window.tasksLimitedInterupt ++;
           data.canStart = false;
           data.taskInterupt = 0;
           console.log('tasks limited interupt='+window.tasksLimitedInterupt);
         }
        }
        
      }
        
    }, 1000);
  

   
  }

  update() {}

  play() {}

  pause() {}

  tick() {}

  remove() {}

  destroy() {}
}

new LimitedInteruption().register();



