import {ComponentWrapper} from '../essential/aframe-wrapper';
import {EntityBuilder} from '../essential/entity-builder';

interface StatisticsComponentSchema {
   canStart: boolean;
}

declare global {
  interface Window { impulsivityScore: number;omissionScore:number}
                      
}

export class StatisticsComponent extends ComponentWrapper<StatisticsComponentSchema> {
  constructor() {
    super('statistics-component', {
        canStart: {
        type: 'boolean',
        default: false,
      },
    });
  }

  init() {
    let _totalNumberOfTasks = 8;
    let _TAS = 8; //Attention time attained by typical child during a task (look at the npc while moving)
    let _TypicalTime = 82; //the time taken by a typical child to finish the game
    let _TaR:number  ;//target ratio
    let _TiR :number ; // time ratio
    let statistics:any;
    let _avgResponseTime :number;
    let allStats={
      myStats:''
    };

    

    if(sessionStorage.getItem("allStats") != null)
    {
      allStats = JSON.parse(sessionStorage.getItem("allStats"));
      console.log("all stats is"+allStats)
    }


    _avgResponseTime = window.TimeResponse/8;
    _TaR = window.tasksLimitedInterupt/_totalNumberOfTasks;
    _TiR = window.timeTaken / _TypicalTime;

    window.impulsivityScore = 1/((-_TaR) * ((Math.log10(_TiR) - 1) + Math.E));
    window.omissionScore = _TAS /( window.AAS + Math.E);
    
    
     statistics = {     
      omissionScore:window.omissionScore,
      ImpulsivityScore:window.impulsivityScore,
      AAS :window.AAS,    
      avgResponseTime:_avgResponseTime,          
      TaR : _TaR,
      TiR : _TiR 
    }
    console.log(statistics);
   
    let date: Date = new Date();  
    allStats[date.toString()] = {...allStats[date.toString()],statistics};
    sessionStorage.setItem('allStats',JSON.stringify(allStats));
    
   // window.open('../../finalPage.html',"_self");
  }
    

  update() {}

  play() {}

  pause() {}

  tick() {}

  remove() {}

  destroy() {}
}

new StatisticsComponent().register();



