import {ComponentWrapper} from '../essential/aframe-wrapper';
import {EntityBuilder} from '../essential/entity-builder';

interface TimeTakenSchema {
   canStart: boolean;
}

declare global {
    interface Window { timeTaken: number;startTime:any }
}



export class timeTakenComponent extends ComponentWrapper<TimeTakenSchema> {
  constructor() {
    super('time-taken', {
        canStart: {
        type: 'boolean',
        default: false,
      },
    });
  }

  init() {
  
    let el = this.el;
    let data = this.data ;
    window.timeTaken = 0;
    let timer:any ;
    let current = new Date();
     window.startTime = current.toLocaleString();
  
    
   timer = setInterval(() => { 
    window.timeTaken ++;   
     
  // console.log('timeTaken'+ window.timeTaken)
     
}, 1000);

 
el.addEventListener('stop',function(){
  clearInterval(timer);
})
    
  
 
   
  }

  update() {}

  play() {}

  pause() {}

  tick() {}

  remove() {}

  destroy() {}
}

new timeTakenComponent().register();



